.homeProduct{
    width: 100%;
    margin: auto;
    margin-top: 5%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    row-gap: 35px;
}
.productImg{
    /* width: 250px; */
    object-fit: cover;
    height: 200px;
    border-radius: 10px;
}
.homeCard{
    width: 300px;
    padding: 20px;
    /* background-color: #232f3e; */
    border-radius: 20px;
}